  <div class="plans-modal">
    <h1>GoToMarket - Instagram</h1>
    <img
      src="@/assets/plans/arrow_back.svg"
      alt="Voltar"
      class="arrow-back-icon"
      @click="$router.push('/home')"
    />

    <h2>
      I.A. de gerenciamento de postagens, coment√°rios e usu√°rios no Instagram.
    </h2>

    <img
      src="@/assets/plans/payments.svg"
      alt="Payment"
      class="payment-image"
    />

    <div class="container-group">
      <div
        v-for="plan in plans"
        :key="plan.id"
        class="container"
        :class="plan.id"
      >
        <h2>{{ formatPrice(plan.price) }}</h2>
        <h2 :class="plan.idClass">{{ plan.name }}</h2>

        <template v-if="plan.id === 'bronze'">
          <h3>AutomatePosts</h3>
          <h4>
            Posts automatizados, aloca√ß√£o de hor√°rio, cria√ß√£o de imagens e
            descri√ß√µes.
          </h4>
        </template>
        <template v-else-if="plan.id === 'silver'">
          <h3>AutomatePosts</h3>
          <h3>AutomateComments</h3>
          <h4>
            Coment√°rios automatizados, resposta de coment√°rios em suas postagens
            e gerenciamento de intera√ß√µes, al√©m do incluso no plano Bronze.
          </h4>
        </template>
        <template v-else>
          <h3>AutomatePosts</h3>
          <h3>AutomateComments</h3>
          <h3>AutomateAdmin</h3>
          <h4>
            Administra√ß√£o automatizada, visualiza√ß√£o e an√°lise de "likes",
            "shares" e seguidores, configura√ß√£o personalizada, al√©m do incluso
            no plano Silver +.
          </h4>
        </template>

        <button
          :disabled="plan.selected"
          :class="{ selected: plan.selected }"
          @click="selectPlan(plan)"
        >
          {{ plan.selected ? 'Selecionado' : 'Selecionar' }}
        </button>
      </div>
    </div>

    <h3 class="advise">
      Valores pass√≠veis de negocia√ß√£o, assim como os produtos em quest√£o. Para
      mais informa√ß√µes, WhatsApp: 000000000000
    </h3>
  </div>
</template>

<script>
export default {
  name: 'PlansModal',
  data() {
    return {
      /* lista base vis√≠vel mesmo se a chamada API falhar */
      plans: [
        { id: 'bronze', name: 'Bronze',   idClass: 'Bronze', price: 49.9, selected: false },
        { id: 'silver', name: 'Silver +', idClass: 'Silver', price: 79.9, selected: false },
        { id: 'gold',   name: 'Gold ++',  idClass: 'Gold',   price: 99.9, selected: false },
      ],
    }
  },
  mounted() {
    this.fetchPlans()
  },
  methods: {
    /* Atualiza apenas a flag de plano j√° contratado */
    async fetchPlans() {
      try {
        const token = localStorage.getItem('token')
        const res = await fetch('/api/plans', {
          headers: { Authorization: `Bearer ${token}` },
        })
        if (!res.ok) throw new Error('Falha ao buscar planos')
        const { plans: serverPlans } = await res.json()

        // sincronia: mant√©m todos os planos vis√≠veis e s√≥ marca selected
        serverPlans.forEach((srv) => {
          const local = this.plans.find((p) => p.id === srv.id)
          if (local) local.selected = !!srv.selected
        })
      } catch (err) {
        console.error(err) // se falhar, lista padr√£o continua vis√≠vel
      }
    },

    formatPrice(v) {
      return v.toLocaleString('pt-BR', {
        style: 'currency',
        currency: 'BRL',
        minimumFractionDigits: 2,
      })
    },

    /* Modificado para redirecionar para a p√°gina de pagamento Kiwify */
    async selectPlan(plan) {
      // Se o plano j√° estiver selecionado, o bot√£o estar√° desabilitado (:disabled="plan.selected").
      // Esta verifica√ß√£o √© uma seguran√ßa adicional.
      if (plan.selected) {
        return;
      }

      // Redireciona o usu√°rio para a p√°gina de pagamento.
      // A aba atual ser√° alterada para esta URL.
      const paymentUrl = 'https://pay.kiwify.com.br/VxMIdHr';
      window.location.href = paymentUrl;

      // Qualquer l√≥gica de backend para registrar a inten√ß√£o de upgrade ou
      // lidar com o status do plano geralmente ocorreria AP√ìS uma confirma√ß√£o
      // de pagamento bem-sucedida vinda da Kiwify (por exemplo, via webhook
      // para o seu backend ou um redirecionamento da Kiwify de volta para o seu site
      // com um status de transa√ß√£o).
      // console.log(`Redirecionando para pagamento do plano ${plan.id}: ${paymentUrl}`);
    },
  },
}
</script>

